import React from "react";
import {
  Box,
  Tabs,
  TabList,
  TabPanels,
  Tab,
  TabPanel,
  IconButton,
  Flex,
  Spacer,
  Heading,
  HStack,
  VStack,
  useColorMode,
  useColorModeValue,
} from "@chakra-ui/react";
import { MoonIcon, SunIcon } from "@chakra-ui/icons";
import FlashcardMode from "./components/FlashcardMode";
import QuizMode from "./components/QuizMode";
import ReviewMode from "./components/ReviewMode";
import flashcardsData from "./data/terms.json";

// Main App component to manage the application UI and navigation
function App() {
  // Extract unique categories from flashcardsData for filtering purposes
  const categories = [...new Set(flashcardsData.map((card) => card.category))];

  // Retrieve current color mode and function to toggle color mode from Chakra UI hook
  const { colorMode, toggleColorMode } = useColorMode();

  // Styling for tabs, changing based on current color mode
  const tabBgColor = useColorModeValue("teal.100", "gray.700"); // Background color of tab when hovered
  const tabSelectedBgColor = useColorModeValue("teal.500", "teal.300"); // Background color of the selected tab
  const tabTextColor = useColorModeValue("teal.800", "white"); // Text color for tabs

  return (
    <Box>
      {/* Navbar at the top with a sticky position */}
      <Flex
        as="nav"
        bg={useColorModeValue("teal.500", "gray.900")} // Background color of the navbar depending on color mode
        p="2" // Padding inside the navbar
        boxShadow="lg" // Adds shadow for depth effect
        alignItems="center" // Align all child elements to the center vertically
        position="sticky" // Makes navbar sticky at the top while scrolling
        top="0" // Stick at the top of the screen
        zIndex="10" // Ensures the navbar appears above other components
        borderBottomWidth={1} // Adds border at the bottom of the navbar
        borderBottomColor={useColorModeValue("gray.200", "gray.700")} // Border color based on color mode
        height="70px" // Set height for the navbar
      >
        {/* Logo and Heading */}
        <HStack spacing="4">
          {/* Box for displaying logo image */}
          <Box boxSize={{ base: "70px", md: "40px" }} mt="2">
            <img
              src="/medical-term-app/book-cover.jpg" // Source of the book cover image
              alt="Book Cover" // Alternate text for the image
              style={{ width: "100%", height: "auto" }} // Responsive image styling
            />
          </Box>
          {/* Title of the app */}
          <VStack align="center" spacing="0">
            <Heading
              size={{ base: "sm", md: "md" }} // Adjust size based on screen size
              color="white" // Text color is set to white
              textAlign="center" // Align the text to the center
            >
              Medical Terminology for Health Professions 9th Edition
            </Heading>
          </VStack>
        </HStack>

        {/* Spacer to push the toggle button to the right */}
        <Spacer />

        {/* Dark/Light mode toggle button */}
        <IconButton
          icon={colorMode === "light" ? <MoonIcon /> : <SunIcon />} // Use MoonIcon for light mode and SunIcon for dark mode
          onClick={toggleColorMode} // Calls the toggle function to switch between dark and light modes
          isRound // Makes the button round in shape
          size="sm" // Set button size
          colorScheme="teal" // Set color scheme to teal
          aria-label="Toggle dark mode" // Accessibility label for screen readers
        />
      </Flex>

      {/* Tabs for navigating between different modes: Flashcards, Quiz, and Review */}
      <Tabs variant="enclosed" colorScheme="teal" id="tabs" mt="4">
        {/* List of tabs */}
        <TabList>
          {/* Flashcards Tab */}
          <Tab
            _selected={{ bg: tabSelectedBgColor, color: "white" }} // Styles for the selected tab
            _hover={{ bg: tabBgColor }} // Styles when the tab is hovered
          >
            Flashcards
          </Tab>

          {/* Quiz Tab */}
          <Tab
            _selected={{ bg: tabSelectedBgColor, color: "white" }} // Styles for the selected tab
            _hover={{ bg: tabBgColor }} // Styles when the tab is hovered
          >
            Quiz
          </Tab>

          {/* Review Tab */}
          <Tab
            _selected={{ bg: tabSelectedBgColor, color: "white" }} // Styles for the selected tab
            _hover={{ bg: tabBgColor }} // Styles when the tab is hovered
          >
            Review
          </Tab>
        </TabList>

        {/* Tab panels to render content for each mode */}
        <TabPanels>
          {/* Flashcard Mode Panel */}
          <TabPanel>
            <FlashcardMode
              flashcardsData={flashcardsData} // Passes the flashcards data to FlashcardMode
              categories={categories} // Passes categories for filtering
            />
          </TabPanel>

          {/* Quiz Mode Panel */}
          <TabPanel>
            <QuizMode
              flashcardsData={flashcardsData} // Passes the flashcards data to QuizMode
              categories={categories} // Passes categories for filtering
            />
          </TabPanel>

          {/* Review Mode Panel */}
          <TabPanel>
            <ReviewMode
              flashcardsData={flashcardsData} // Passes the flashcards data to ReviewMode
              categories={categories} // Passes categories for filtering
            />
          </TabPanel>
        </TabPanels>
      </Tabs>
    </Box>
  );
}

export default App;
