import React, { useState, useRef } from "react";
import { Box, Text, Image, Flex, useColorModeValue } from "@chakra-ui/react";
import audioOnIcon from "../assets/icons/audio_on.png"; // Icon for audio playing state
import audioOffIcon from "../assets/icons/audio_off.png"; // Icon for audio off state
import { Howl } from "howler"; // Howler library for handling audio playback

// Global reference to keep track of the currently playing audio instance
let currentPlayingAudio = null;

const ReviewCard = ({ card }) => {
  // State to track whether the audio is playing or not
  const [isPlaying, setIsPlaying] = useState(false);
  // Reference to store the audio instance for the current card
  const audioRef = useRef(null);

  // Color styles for light and dark modes
  const cardBg = useColorModeValue("white", "gray.700"); // Background color for the card
  const cardColor = useColorModeValue("gray.800", "whiteAlpha.900"); // Text color for the card
  const boxShadow = useColorModeValue(
    "0 4px 8px rgba(0, 0, 0, 0.15)",
    "0 4px 8px rgba(0, 0, 0, 0.9)",
  ); // Box shadow for light and dark mode
  const audioIconColor = useColorModeValue("teal.700", "teal.300"); // Color of the term text
  const pronunciationColor = useColorModeValue("gray.600", "gray.400"); // Color for the pronunciation text
  const definitionColor = useColorModeValue("gray.700", "gray.300"); // Color for the definition text

  // If the card is not provided, return null to avoid rendering errors
  if (!card) {
    return null;
  }

  // Function to handle playing the audio
  const playAudio = () => {
    // If audio is already playing, do nothing
    if (isPlaying) {
      return;
    }

    // Stop any currently playing audio if it's not the current card's audio
    if (currentPlayingAudio && currentPlayingAudio !== audioRef.current) {
      currentPlayingAudio.stop();
    }

    // Create a new Howl audio instance if it's not set yet for the current card
    if (!audioRef.current) {
      // Extract the chapter number from the category field of the card
      const chapterNumber = card.category.match(/Ch(\d+)/)?.[1];
      // Construct the path to the audio file
      const audioPath = `/medical-term-app/audio/ch${chapterNumber}_audio/${card.term
        .toLowerCase()
        .replace(/ /g, "_")}.mp3`;

      // Create the Howl instance to play the audio
      audioRef.current = new Howl({
        src: [audioPath], // Source of the audio file
        html5: true, // Use HTML5 for audio playback to support more devices
        onplay: () => {
          setIsPlaying(true); // Set playing state to true when the audio starts
          currentPlayingAudio = audioRef.current; // Track the currently playing audio globally
        },
        onend: () => {
          setIsPlaying(false); // Set playing state to false when the audio ends
          audioRef.current = null; // Clear the reference when the audio finishes playing
          if (currentPlayingAudio === audioRef.current) {
            currentPlayingAudio = null; // Clear global reference if this audio was playing
          }
        },
        onstop: () => {
          setIsPlaying(false); // Set playing state to false when the audio is stopped
          audioRef.current = null; // Clear the reference when stopped
          if (currentPlayingAudio === audioRef.current) {
            currentPlayingAudio = null; // Clear global reference if this audio was stopped
          }
        },
      });
    }

    // Play the audio
    audioRef.current.play();
  };

  // Function to handle stopping the audio
  const stopAudio = () => {
    // Stop the audio if it is currently playing
    if (audioRef.current && isPlaying) {
      audioRef.current.stop();
    }
  };

  // Function to handle toggling audio play/pause
  const handleAudioToggle = () => {
    if (isPlaying) {
      // Stop the audio if it is currently playing
      stopAudio();
    } else {
      // Play the audio if it is not currently playing
      playAudio();
    }
  };

  // Function to render the pronunciation with special styling for capitalized segments
  const renderPronunciation = (pronunciation) => {
    return pronunciation.split(/(\s|-)/).map((segment, index) => {
      if (segment.match(/[A-Z]+/)) {
        // Apply bold and colored styling to capitalized segments
        return (
          <span key={index} style={{ fontWeight: "bold", color: "#e53e3e" }}>
            {segment}
          </span>
        );
      } else {
        // Return normal styling for other segments
        return <span key={index}>{segment}</span>;
      }
    });
  };

  return (
    <Box
      width="300px" // Set width of the card
      margin="20px auto" // Center the card with top and bottom margins
      padding="20px" // Padding inside the card for spacing
      borderRadius="8px" // Rounded corners for the card
      boxShadow={boxShadow} // Box shadow to give a slight depth effect
      backgroundColor={cardBg} // Background color for the card based on light/dark mode
      color={cardColor} // Text color for the card based on light/dark mode
      textAlign="center" // Center-align text inside the card
      position="relative" // Relative position for the card
    >
      <Flex justify="center" align="center">
        {/* Audio Icon for toggling audio play */}
        <Image
          src={isPlaying ? audioOnIcon : audioOffIcon} // Show the appropriate icon based on audio playing state
          alt="Audio Icon" // Alt text for accessibility
          boxSize="20px" // Set the size of the audio icon
          onClick={handleAudioToggle} // Handle audio toggle when clicked
          cursor="pointer" // Show pointer cursor to indicate clickability
          mr="10px" // Add right margin to create space between the icon and text
        />
        {/* Display the term of the card */}
        <Text fontSize="xl" fontWeight="bold" color={audioIconColor}>
          {card.term}
        </Text>
      </Flex>
      {/* Display the pronunciation if available */}
      {card.pronunciation && (
        <Text fontSize="md" color={pronunciationColor} mt="2">
          <em>{renderPronunciation(card.pronunciation)}</em>
        </Text>
      )}
      {/* Display the definition of the term */}
      <Text fontSize="md" color={definitionColor} mt="4">
        {card.definition}
      </Text>
    </Box>
  );
};

export default ReviewCard;
