// Import necessary dependencies from React, ReactDOM, Chakra UI, and local files
import React from "react";
import ReactDOM from "react-dom/client";
import { ChakraProvider, ColorModeScript } from "@chakra-ui/react";
import App from "./App";
import theme from "./components/theme"; // Import the custom theme to be used throughout the app
import "./index.css"; // Import global CSS for general styling
import "@fontsource/m-plus-1p"; // Import the "M PLUS 1p" font from the @fontsource package

// Initialize the root for the React application
const root = ReactDOM.createRoot(document.getElementById("root"));

root.render(
  // StrictMode is a wrapper to identify potential issues during development, such as unsafe lifecycles or deprecated APIs.
  <React.StrictMode>
    {/* ChakraProvider makes the Chakra UI components available throughout the app and applies the custom theme */}
    <ChakraProvider theme={theme}>
      {/* ColorModeScript adds a script that helps persist the color mode (light/dark) between sessions */}
      <ColorModeScript initialColorMode={theme.config.initialColorMode} />
      {/* Render the main App component which contains the entire application */}
      <App />
    </ChakraProvider>
  </React.StrictMode>,
);
