import React, { useState, useRef } from "react";
import { Center, Flex, Button, Text, Box, VStack } from "@chakra-ui/react";
import CategorySelect from "./CategorySelect";
import ReviewCard from "./ReviewCard";
import { useColorModeValue } from "@chakra-ui/react";

const ReviewMode = ({ flashcardsData, categories }) => {
  // State for the selected category (default is "All")
  const [selectedCategory, setSelectedCategory] = useState("All");
  // State for storing the cards to be shown based on the selected category or letter
  const [cardsToShow, setCardsToShow] = useState(flashcardsData);
  // State for the currently selected alphabet letter for filtering
  const [currentLetter, setCurrentLetter] = useState(null);
  // Reference for the card container to facilitate scrolling
  const cardContainerRef = useRef(null);

  // Color mode values for different background and text colors depending on light or dark mode
  const bg = useColorModeValue("white", "gray.700");
  const color = useColorModeValue("gray.800", "whiteAlpha.900");
  const navBg = useColorModeValue("whiteAlpha.900", "gray.800");
  const navBorderColor = useColorModeValue("gray.200", "gray.600");

  // Function to handle category changes
  const handleCategoryChange = (e) => {
    const category = e.target.value; // Get the selected category value
    setSelectedCategory(category); // Update selected category state
    // Filter flashcards based on selected category
    const filteredCards =
      category === "All"
        ? flashcardsData // Show all cards if category is "All"
        : flashcardsData.filter((card) => card.category === category);
    setCardsToShow(filteredCards); // Update cards to be shown
    setCurrentLetter(null); // Reset current letter when category changes
  };

  // Function to handle letter button clicks
  const handleLetterClick = (letter) => {
    setCurrentLetter(letter); // Set the selected letter for filtering

    // Scroll to the top of the window after filtering to provide a smooth UX
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  // Filter cards based on the selected letter
  // If a letter is selected, only show cards starting with that letter
  const filteredCards = currentLetter
    ? cardsToShow.filter(
        (card) => card.term.charAt(0).toUpperCase() === currentLetter,
      )
    : cardsToShow;

  // Create an array of all letters in the alphabet
  const alphabet = "ABCDEFGHIJKLMNOPQRSTUVWXYZ".split("");

  // Determine which letters have flashcards starting with them
  const lettersWithTerms = new Set(
    cardsToShow.map((card) => card.term.charAt(0).toUpperCase()),
  );

  return (
    <Center
      flexDirection="column"
      padding={{ base: "10px", md: "20px" }}
      bg={bg} // Background color for light/dark mode
      color={color} // Text color for light/dark mode
    >
      {/* Category Selection */}
      <Box
        mb={{ base: "10px", md: "20px" }} // Margin bottom to create spacing
        width="100%"
        maxWidth="600px" // Limit the width to avoid overflow
        textAlign="center"
        marginLeft={{ base: "-5", md: "-20px" }} // Adjust the positioning for responsiveness
      >
        <CategorySelect
          categories={["All", ...categories]} // Add "All" to the categories list for filtering
          selectedCategory={selectedCategory} // Bind the currently selected category
          handleCategoryChange={handleCategoryChange} // Pass function to handle category change
        />
      </Box>

      {/* Displaying Review Cards */}
      <Flex
        ref={cardContainerRef} // Reference for card container
        width="100%"
        maxWidth="800px" // Limit the width to ensure readability
        position="relative"
        justifyContent="center"
        alignItems="flex-start"
        marginRight="60px" // Add margin to ensure A-Z navigation does not cover the cards
      >
        {/* Render all filtered cards */}
        <Box>
          {filteredCards.length > 0 ? (
            // Map over all the filtered cards and display them using ReviewCard component
            filteredCards.map((card, index) =>
              card ? <ReviewCard key={index} card={card} /> : null,
            )
          ) : (
            // Show message when no cards are available
            <Text fontSize="xl" mt="20px">
              No terms available.
            </Text>
          )}
        </Box>

        {/* A-Z Navigation Buttons Vertically on the Right in a Box */}
        <Box
          position="fixed" // Fix position to remain on the screen during scroll
          right="5px" // Position to the right side of the viewport
          top="50%" // Center the A-Z navigation box vertically in the viewport
          transform="translateY(-50%)" // Adjust for true centering
          maxHeight={{ base: "65%", md: "65%" }} // Set max height to prevent overflow
          overflowY="auto" // Enable scrolling if content exceeds max height
          zIndex="1" // Ensure navigation appears on top of other content
          padding="10px" // Padding around the navigation buttons
          borderRadius="md" // Rounded corners for better aesthetics
          boxShadow="md" // Shadow effect for visual depth
          backgroundColor={navBg} // Background color for light/dark mode
          borderWidth="1px" // Border width
          borderColor={navBorderColor} // Border color for light/dark mode
        >
          <VStack spacing="4px">
            {alphabet.map((letter) => (
              <Button
                key={letter}
                size={{ base: "sm", md: "sm" }} // Button size for responsive design
                width="40px" // Button width
                onClick={() => handleLetterClick(letter)} // Handle letter button click
                variant={currentLetter === letter ? "solid" : "outline"} // Change variant based on selected letter
                colorScheme="teal" // Color scheme for the button
                opacity={lettersWithTerms.has(letter) ? 1 : 0.3} // Adjust opacity if no terms start with the letter
                isDisabled={!lettersWithTerms.has(letter)} // Disable button if no terms available for that letter
              >
                {letter}
              </Button>
            ))}
            <Button
              size={{ base: "xs", md: "sm" }} // Button size for responsive design
              width="40px" // Button width
              onClick={() => {
                setCurrentLetter(null); // Reset the letter filter
                window.scrollTo({ top: 0, behavior: "smooth" }); // Scroll to top smoothly
              }}
              variant={!currentLetter ? "solid" : "outline"} // Change variant based on current filter state
              colorScheme="teal" // Color scheme for the button
            >
              All
            </Button>
          </VStack>
        </Box>
      </Flex>
    </Center>
  );
};

export default ReviewMode;
