import React, { useState, useEffect } from "react";
import { Box, Text, Image, Flex, useColorModeValue } from "@chakra-ui/react";
import { Howl } from "howler";
import audioOnIcon from "../assets/icons/audio_on.png";
import audioOffIcon from "../assets/icons/audio_off.png";

// Global reference to keep track of the currently playing audio, ensuring only one audio file plays at a time
let currentPlayingAudio = null;

// Flashcard component responsible for displaying a flashcard and allowing users to interact with it
const Flashcard = ({ card, isFlipped, onFlip }) => {
  // Local state to keep track of the card data (used for flipping transition)
  const [localCard, setLocalCard] = useState(card);
  // State to keep track of whether audio is currently playing
  const [isPlaying, setIsPlaying] = useState(false);

  // Styles for light and dark mode using Chakra UI's useColorModeValue hook
  const cardBgFront = useColorModeValue("white", "gray.700"); // Background color for the front of the card
  const cardBgBack = useColorModeValue("#e3fcef", "gray.600"); // Background color for the back of the card
  const cardTextColor = useColorModeValue("gray.800", "whiteAlpha.900"); // Text color for the card
  const pronunciationColor = useColorModeValue("gray.600", "gray.400"); // Pronunciation text color
  const definitionColor = useColorModeValue("gray.700", "whiteAlpha.800"); // Definition text color
  const termColor = useColorModeValue("teal.700", "teal.300"); // Color for the term text
  const boxShadow = useColorModeValue(
    "0 4px 8px rgba(0, 0, 0, 0.15)",
    "0 4px 8px rgba(0, 0, 0, 0.9)",
  ); // Box shadow based on light or dark mode

  // Update local card state when the card prop changes, with a delay for a smooth flipping experience
  useEffect(() => {
    if (!isFlipped) {
      const timeout = setTimeout(() => {
        setLocalCard(card);
      }, 250); // Delay to synchronize card update with flipping animation
      return () => clearTimeout(timeout); // Cleanup timeout if the component is unmounted or props change
    }
  }, [card, isFlipped]);

  // Cleanup effect to stop playing audio when the component is unmounted
  useEffect(() => {
    return () => {
      if (currentPlayingAudio) {
        currentPlayingAudio.stop(); // Stop the current playing audio if it exists
        currentPlayingAudio = null;
      }
    };
  }, []);

  // Function to play audio of the term pronunciation
  const playAudio = () => {
    if (!card || !card.category) {
      console.error("Card or card category is missing"); // Log error if card or category is not available
      return;
    }

    // Stop any currently playing audio to ensure only one audio file is played at a time
    if (currentPlayingAudio) {
      currentPlayingAudio.stop();
    }

    // Extract chapter number from the card category using regular expression
    const chapterMatch = card.category.match(/Ch(\d+)/);
    if (!chapterMatch) {
      console.error("Invalid card category format"); // Log error if category format is invalid
      return;
    }

    // Construct the path to the audio file
    const chapterNumber = chapterMatch[1];
    const audioPath = `/medical-term-app/audio/ch${chapterNumber}_audio/${card.term
      .toLowerCase()
      .replace(/ /g, "_")}.mp3`;

    // Initialize Howler audio instance to play the audio
    const sound = new Howl({
      src: [audioPath],
      volume: 0.5, // Set volume level
      html5: true, // Use HTML5 audio for better compatibility
      onplay: () => {
        setIsPlaying(true); // Update state to indicate audio is playing
        currentPlayingAudio = sound; // Set the current playing audio to this instance
      },
      onend: () => {
        setIsPlaying(false); // Update state to indicate audio has stopped playing
        currentPlayingAudio = null; // Clear reference to the audio instance
      },
      onstop: () => {
        setIsPlaying(false); // Update state to indicate audio has stopped
        if (currentPlayingAudio === sound) {
          currentPlayingAudio = null; // Clear reference if the stopped audio was the current playing audio
        }
      },
      onloaderror: (id, err) => {
        console.error("Error loading audio:", err); // Log error if there is an issue loading the audio
        setIsPlaying(false);
      },
      onplayerror: (id, err) => {
        console.error("Error playing audio:", err); // Log error if there is an issue playing the audio
        sound.once("unlock", () => sound.play()); // Attempt to play audio again after unlocking (iOS-specific workaround)
      },
    });

    // Play the sound
    sound.play();
  };

  // Function to stop the currently playing audio
  const stopAudio = () => {
    if (currentPlayingAudio) {
      currentPlayingAudio.stop(); // Stop the audio if it is playing
    }
  };

  // Handle clicking on the audio toggle button to play or stop audio
  const handleAudioToggle = (event) => {
    event.stopPropagation(); // Prevent card flip when clicking on the audio icon
    if (isPlaying) {
      stopAudio(); // Stop audio if it is currently playing
    } else {
      playAudio(); // Play audio if it is not playing
    }
  };

  // Handle card flip when the card is clicked
  const handleFlip = () => {
    onFlip(); // Invoke the onFlip function passed as a prop to flip the card
  };

  // Function to render pronunciation with special styling for capitalized segments
  const renderPronunciation = (pronunciation) => {
    return pronunciation.split(/(\s|-)/).map((segment, index) => {
      if (segment.match(/[A-Z]+/)) {
        // Apply special styling to capitalized segments
        return (
          <span key={index} style={{ fontWeight: "bold", color: "#e53e3e" }}>
            {segment}
          </span>
        );
      } else {
        return <span key={index}>{segment}</span>;
      }
    });
  };

  return (
    <Box
      className={`flashcard ${isFlipped ? "flipped" : ""}`} // Apply different class based on flipped state
      sx={{
        width: "300px",
        height: "200px",
        perspective: "1000px", // Perspective for 3D effect on flip
        cursor: "pointer",
        margin: "20px auto",
        position: "relative",
      }}
      onClick={handleFlip} // Flip the card when the box is clicked
    >
      {/* Box for shadows behind the flashcard for styling */}
      <Box
        _before={{
          content: '""',
          position: "absolute",
          top: "0",
          left: "0",
          width: "100%",
          height: "100%",
          borderRadius: "8px",
          boxShadow: "0 8px 16px rgba(0, 0, 0, 0.15)",
          transform: "rotate(-2deg)",
          zIndex: "-1",
        }}
        _after={{
          content: '""',
          position: "absolute",
          top: "0",
          left: "0",
          width: "100%",
          height: "100%",
          borderRadius: "8px",
          boxShadow: "0 12px 24px rgba(0, 0, 0, 0.1)",
          transform: "rotate(2deg)",
          zIndex: "-2",
        }}
      />

      {/* Inner box for the card that handles flipping */}
      <Box
        className="flashcard-inner"
        sx={{
          position: "relative",
          width: "100%",
          height: "100%",
          textAlign: "center",
          transition: "transform 0.6s ease-in-out", // Smooth flipping transition
          transformStyle: "preserve-3d",
          transform: isFlipped ? "rotateY(180deg)" : "rotateY(0deg)", // Rotate card based on flipped state
        }}
      >
        {/* Front Side (Term and Pronunciation) */}
        <Box
          className="flashcard-front"
          sx={{
            position: "absolute",
            width: "100%",
            height: "100%",
            backfaceVisibility: "hidden", // Hide back side when viewing the front
            borderRadius: "8px",
            boxShadow: boxShadow,
            backgroundColor: cardBgFront,
            color: cardTextColor,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            fontWeight: "bold",
            flexDirection: "column",
            padding: "10px",
          }}
        >
          <Flex justify="center" align="center">
            {/* Audio Icon for playing/stopping audio */}
            <Image
              src={isPlaying ? audioOnIcon : audioOffIcon}
              alt="Audio Icon"
              boxSize="30px"
              onClick={handleAudioToggle}
              cursor="pointer"
              mr="10px"
            />
            {/* Display the term */}
            <Text fontSize="25px" color={termColor}>
              {localCard.term}
            </Text>
          </Flex>
          {/* Display the pronunciation if available */}
          {localCard.pronunciation && (
            <Text fontSize="md" color={pronunciationColor} mt="2">
              <em>{renderPronunciation(localCard.pronunciation)}</em>
            </Text>
          )}
        </Box>

        {/* Back Side (Definition) */}
        <Box
          className="flashcard-back"
          sx={{
            position: "absolute",
            width: "100%",
            height: "100%",
            backfaceVisibility: "hidden", // Hide front side when viewing the back
            borderRadius: "8px",
            boxShadow: boxShadow,
            backgroundColor: cardBgBack,
            color: cardTextColor,
            transform: "rotateY(180deg)", // Rotate to display back side
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            padding: "10px",
          }}
        >
          {/* Display the definition */}
          <Text fontSize="md" color={definitionColor}>
            {localCard.definition}
          </Text>
        </Box>
      </Box>
    </Box>
  );
};

export default Flashcard;
