import React, { useState, useEffect } from "react";
import { Center, Button, Text, Box } from "@chakra-ui/react";
import Flashcard from "./Flashcard";
import CategorySelect from "./CategorySelect";
import { useColorModeValue } from "@chakra-ui/react";

// FlashcardMode component is responsible for displaying flashcards in a selected category
// and allowing users to navigate through them, shuffle them, and flip the cards.
const FlashcardMode = ({ flashcardsData, categories }) => {
  // State to keep track of the current flashcard index
  const [currentIndex, setCurrentIndex] = useState(0);
  // State to keep track of the selected category
  const [selectedCategory, setSelectedCategory] = useState("All");
  // State to keep track of the cards to be shown after filtering based on category
  const [cardsToShow, setCardsToShow] = useState(flashcardsData);
  // State to keep track if the flashcard is flipped (showing definition)
  const [isFlipped, setIsFlipped] = useState(false);

  // Styles for light and dark mode using Chakra UI's useColorModeValue hook
  const bg = useColorModeValue("white", "gray.700"); // Background color based on light/dark mode
  const color = useColorModeValue("gray.800", "whiteAlpha.900"); // Text color based on light/dark mode

  // Function to shuffle the flashcards and reset the current index
  const shuffleFlashcards = () => {
    // Shuffle the current set of cards using a random sort function
    const shuffledCards = [...cardsToShow].sort(() => Math.random() - 0.5);
    setCardsToShow(shuffledCards);
    setCurrentIndex(0); // Reset to first card after shuffle
    setIsFlipped(false); // Unflip any flipped card
  };

  // Handle change in selected category and update the flashcards to display accordingly
  const handleCategoryChange = (e) => {
    const category = e.target.value;
    setSelectedCategory(category);
    // Filter flashcards based on selected category or show all cards if "All" is selected
    const filteredCards =
      category === "All"
        ? flashcardsData
        : flashcardsData.filter((card) => card.category === category);
    setCardsToShow(filteredCards);
    setCurrentIndex(0); // Reset to first card
    setIsFlipped(false); // Unflip any flipped card
  };

  // Function to navigate to the next card
  const nextCard = () => {
    if (isFlipped) {
      // If the card is flipped, unflip it before navigating to the next one
      setIsFlipped(false);
      setTimeout(() => {
        setCurrentIndex((prevIndex) =>
          prevIndex + 1 < cardsToShow.length ? prevIndex + 1 : prevIndex,
        );
      }, 50); // Delay for smoother transition during card unflip
    } else {
      setCurrentIndex((prevIndex) =>
        prevIndex + 1 < cardsToShow.length ? prevIndex + 1 : prevIndex,
      );
    }
  };

  // Function to navigate to the previous card
  const prevCard = () => {
    if (isFlipped) {
      // If the card is flipped, unflip it before navigating to the previous one
      setIsFlipped(false);
      setTimeout(() => {
        setCurrentIndex((prevIndex) =>
          prevIndex - 1 >= 0 ? prevIndex - 1 : prevIndex,
        );
      }, 50); // Delay for smoother transition during card unflip
    } else {
      setCurrentIndex((prevIndex) =>
        prevIndex - 1 >= 0 ? prevIndex - 1 : prevIndex,
      );
    }
  };

  // Function to handle card flip (toggles between term and definition)
  const handleCardFlip = () => {
    setIsFlipped((prevState) => !prevState);
  };

  // useEffect to re-apply category filtering when selectedCategory changes
  useEffect(() => {
    handleCategoryChange({ target: { value: selectedCategory } });
  }, [selectedCategory]);

  return (
    <Center flexDirection="column" padding="20px" bg={bg} color={color}>
      {/* Category selection box */}
      <Box mb="20px" width="100%" maxWidth="600px" textAlign="center">
        <Box maxWidth="300px" width="100%" mx="auto">
          <CategorySelect
            categories={["All", ...categories]} // Categories dropdown with "All" option
            selectedCategory={selectedCategory} // Currently selected category
            handleCategoryChange={handleCategoryChange} // Function to handle category change
          />
        </Box>
        {/* Button to shuffle flashcards */}
        <Button mt="50px" colorScheme="teal" onClick={shuffleFlashcards}>
          Shuffle Flashcards
        </Button>
      </Box>

      {/* Render flashcard if available, otherwise show no flashcards message */}
      {cardsToShow.length > 0 ? (
        <Flashcard
          card={cardsToShow[currentIndex]} // Current flashcard to display
          isFlipped={isFlipped} // Whether the card is flipped or not
          onFlip={handleCardFlip} // Function to flip the card
          bg={bg} // Background color for the flashcard (based on color mode)
          color={color} // Text color for the flashcard (based on color mode)
        />
      ) : (
        // If no flashcards are available, show a message to the user
        <Text fontSize="xl" mt="20px">
          No flashcards available.
        </Text>
      )}

      {/* Navigation buttons to navigate through flashcards */}
      {cardsToShow.length > 0 && (
        <Center mt="20px" alignItems="center">
          {/* Previous button */}
          <Button
            onClick={prevCard}
            mr="60px"
            colorScheme="teal"
            isDisabled={currentIndex === 0} // Disable if currently on the first card
          >
            Previous
          </Button>
          {/* Display current card index */}
          <Text fontSize="lg">
            {currentIndex + 1}/{cardsToShow.length}{" "}
            {/* Show current card out of total */}
          </Text>
          {/* Next button */}
          <Button
            onClick={nextCard}
            ml="60px"
            colorScheme="teal"
            isDisabled={currentIndex === cardsToShow.length - 1} // Disable if currently on the last card
          >
            Next
          </Button>
        </Center>
      )}
    </Center>
  );
};

export default FlashcardMode;
