import React from "react";
import { Select } from "@chakra-ui/react";

// CategorySelect component is a dropdown menu to select flashcard categories
const CategorySelect = ({
  categories, // Array of category names to populate the dropdown
  selectedCategory, // Currently selected category value
  handleCategoryChange, // Function to handle changes when a new category is selected
}) => {
  return (
    <Select
      value={selectedCategory} // Sets the current value of the select dropdown to the selected category
      onChange={handleCategoryChange} // Triggered when a different category is selected by the user
      maxWidth="300px" // Limits the maximum width of the dropdown to 300px for consistent UI
      mb="20px" // Adds margin at the bottom of the select box to create spacing from other elements
    >
      {/* Map through each category in the categories array and create an <option> element */}
      {categories.map((category, index) => (
        <option key={index} value={category}>
          {category} {/* Display the category name */}
        </option>
      ))}
    </Select>
  );
};

export default CategorySelect;
